import React, { useState, useLayoutEffect } from "react";
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Grid from '@mui/material/Grid';
import TabPanel from './TabPanel';
import Breeds from '../json_datas/breeds.json';
import Categories from '../json_datas/categories.json';

const SearchForm = (props) => {
  const [tabNo, setTabNo] = useState(0);
  const [limit, setLimit] = useState(10);
  const [breed, setBreed] = useState('');
  const [category, setCategory] = useState(0);

  useLayoutEffect(() => {
    if (Breeds.length > 0) {
      setBreed(Breeds[0].id);
    }
    if (Categories.length > 0) {
      setCategory(Categories[0].id);
    }
  }, [])

  const handleTabChange = (event, tabNo) => {
    setTabNo(tabNo);
  };

  const a11yProps = (index) => {
    return {
      id: `simple-tab-${index}`,
      'aria-controls': `simple-tabpanel-${index}`,
    };
  }

  const handleLimitChange = (event) => {
    setLimit(event.target.value === '' ? '' : Number(event.target.value));
  };

  const handleOnClickSearch = () => {
    if (tabNo === 0) {
      // ランダム検索
      props.onSearchRandom(limit);
    } else if (tabNo === 1) {
      // 品種検索
      props.onSearchByBreed(limit, breed);
    } else if (tabNo === 2) {
      // カテゴリ検索
      props.onSearchByCategory(limit, category);
    }
  }

  return (
    <Box sx={{ width: '100%' }}>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={tabNo} onChange={handleTabChange} variant="fullWidth" aria-label="basic tabs example">
          <Tab label="おまかせ" {...a11yProps(0)} />
          <Tab label="品種で探す" {...a11yProps(1)} />
          <Tab label="カテゴリで探す" {...a11yProps(2)} />
        </Tabs>
      </Box>
      <TabPanel value={tabNo} index={0}>
      </TabPanel>
      <TabPanel value={tabNo} index={1}>
        <div>
          <Grid container 
                direction="row"
                justifyContent="center"
                alignItems="center">
            <Grid item>
              <FormControl sx={{ m: 1, minWidth: 120 }}>
                <InputLabel id="breed-label" size='small'>品種</InputLabel>
                <Select
                  labelId="breed-label"
                  id="breed-select"
                  value={breed}
                  label="品種"
                  size='small'
                  onChange={(e) => setBreed(e.target.value)}
                  sx={{
                    width: '15rem'
                  }}
                >
                  {Breeds.map((item) => (
                    <MenuItem value={item.id} key={item.id}>{item.name_jp}</MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
          </Grid>
        </div>
      </TabPanel>
      <TabPanel value={tabNo} index={2}>
        <Grid container 
                direction="row"
                justifyContent="center"
                alignItems="center">
          <Grid item>
            <FormControl sx={{ m: 1, minWidth: 120 }}>
              <InputLabel id="category-label" size='small'>カテゴリ</InputLabel>
              <Select
                labelId="category-label"
                id="category-select"
                value={category}
                label="カテゴリ"
                size='small'
                onChange={(e) => setCategory(e.target.value)}
                sx={{
                  width: '15rem'
                }}
              >
                {Categories.map((item) => (
                  <MenuItem value={item.id} key={item.id}>{item.name_jp}</MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
        </Grid>
      </TabPanel>
      <Grid container 
            direction="row"
            justifyContent="center"
            alignItems="center" 
            spacing={2}>
        <Grid item>
          <FormControl sx={{ m: 1, minWidth: 120 }}>
          <InputLabel id="limit-select-label" size='small'>件数</InputLabel>
          <Select
            labelId="limit-select-label"
            id="limit-select"
            value={limit}
            label="Limit"
            size='small'
            onChange={handleLimitChange}
          >
            <MenuItem value={10}>10</MenuItem>
            <MenuItem value={20}>20</MenuItem>
            <MenuItem value={30}>30</MenuItem>
            <MenuItem value={40}>40</MenuItem>
            <MenuItem value={50}>50</MenuItem>
            <MenuItem value={60}>60</MenuItem>
            <MenuItem value={70}>70</MenuItem>
            <MenuItem value={80}>80</MenuItem>
            <MenuItem value={90}>90</MenuItem>
            <MenuItem value={100}>100</MenuItem>
          </Select>
          </FormControl>
        </Grid>
        <Grid item>
          <Button sx={{width: 120 }} variant="outlined" onClick={handleOnClickSearch}>さがす</Button>
        </Grid>
      </Grid>
    </Box>
  );
}

export default SearchForm;