import './App.css';
import React, { useState } from "react";
import axios from 'axios';
import { Header } from './components/Header';
import SearchForm from './components/SearchForm';
import CatList from './components/CatImageList';
import LoadingCircular from './components/LoadingCircular'

const App = () => {
  const url = process.env.REACT_APP_CAT_API_URL;
  const apiKey = process.env.REACT_APP_CAT_API_KEY;
  const [cats, setCats] = useState([]);
  const [loading, setLoading] = useState(false);

  const handleSearchRandom = (limit) => {
    let query = url + '?limit=' + limit;
    console.log(query);
    setLoading(true);
    axios.get(query, { headers: { 'x-api-key': apiKey } })
    .then(res => {
      setCats(res.data);
      setLoading(false);
    })
  };

  const handleSearchByBreed = (limit, breed) => {
    let query = url + '?limit=' + limit + '&breed_ids=' + breed;
    console.log(query);
    setLoading(true);
    axios.get(query, { headers: { 'x-api-key': apiKey } })
    .then(res => {
      setCats(res.data);
      setLoading(false);
    })
  };

  const handleSearchByCategory = (limit, category) => {
    let query = url + '?limit=' + limit + '&category_ids=' + category;
    console.log(query);
    setLoading(true);
    axios.get(query, { headers: { 'x-api-key': apiKey } })
    .then(res => {
      setCats(res.data);
      setLoading(false);
    })
  };

  return (
    <div className="App">
      <Header />
      <SearchForm onSearchRandom={handleSearchRandom} onSearchByBreed={handleSearchByBreed} onSearchByCategory={handleSearchByCategory} />
      { loading ? <LoadingCircular loading={loading} /> : <CatList cats={cats} /> }
    </div>
  );
}

export default App;
